.task-bar {
  /* width: 71.5vw; */
  border-radius: 6px;
  border: 1px solid rgba(119, 119, 119, 0.199);
  padding: 0px 2px;
  margin-bottom: 2px;
  background-color: #dfe6e957;
  height: 4vh;
}

.task-bar:hover {
  /* background-color: #0984e3; */
  background-color: rgb(223, 223, 223);
  /* border-bottom: 1px solid #00a8ff; */
  box-shadow: 0px 0px 3px 2px rgba(209, 209, 209, 0.171);
  border-radius: 0px 0px 2px 2px;
}
.title-box {
  /* width: 100%; */
  /* margin-left: 2vw; */
  font-size: 12px;
  height: 5vh;
}

.title-box h2:hover {
  outline: 1px dotted #74747486;
  /* width: 5vw; */
  /* margin-left: 20px; */
  background-color: rgba(255, 255, 255, 0.445);
  color: #0053be7c;
}
.task-bar h2 {
  cursor: pointer;
  /* max-width: 15vw; */
  padding: 0px 20px;
  margin-left: 2vw;
  font-size: 14px;
  width: 10vw;
  white-space: nowrap;
  overflow: hidden;
  /* text-overflow: ellipsis; */
}

.task-bar-columns-container {
  /* width: 70%; */
  /* width: 40vw; */
  font-size: 11px;
}
.task-bar-columns-container p {
  border-left: 1px solid rgba(119, 119, 119, 0.356);
  padding: 0px 5px;
  margin: 0px;
  text-align: center;
  height: 4vh;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 5vw;
  font-size: 13px;
}

.box-div {
  border-left: 1px solid rgba(119, 119, 119, 0.356);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 0px;
  min-width: 5vw;
  width: 5vw;
  cursor: pointer;
  height: 4vh;
  font-weight: bold;
}

.box-div:hover {
  border-left: 1px dotted #74747486;
}
.task-bar-columns-container img {
  height: 20px;
}

.task-bar-icon {
  margin-right: 10px;
}
.task-bar-title-container {
  width: 100%;
}

.task-bar-icon img {
  width: 20px;

  /* justify-self: last baseline; */
  /* margin-left: 30%;
  margin-right: 10px; */

  cursor: pointer;
}

.task-color-box {
  width: 1vw;
  background-color: #0984e3;
  color: #0984e3;
  padding: 0px 10px;
  z-index: 10;
  margin-left: -10px;
}

.task-color-box:hover {
  width: 2vw;
  background-color: #0984e3;
}

.add-task-bar {
  border-radius: 6px;
  border: 1px solid rgba(119, 119, 119, 0.199);
  padding: 0px 2px;
  margin-bottom: 1px;
  background-color: #dfe6e957;
  height: 4vh;
  width: 71.5vw;
  /* margin: 0px 1vw; */
}

.add-task-bar input {
  width: 100%;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  padding-left: 10px;
}

.add-task-bar button {
  /* width: 100%; */
  background-color: #00a8ff;
  color: white;
  border: none;
}

.delete-icon {
  width: 20px;
  /* transform: rotate(-180deg); */
  margin-left: -25px;
  opacity: 0.4;
  cursor: pointer;
}

.delete-icon:hover {
  transform: scale(1.1);
  opacity: 1;
}

.input-edit-taskName {
  /* width: 10vw; */
  border: none;
  /* margin-left: 40px; */
  background-color: rgba(245, 245, 245, 0.322);
  font-size: 14px;
  outline: 1px dotted #74747486;
  /* padding: 0px 20px; */
  margin-left: 2vw;
  height: 3vh;
  /* margin */
  /* text-align: start; */
  /* position: relative; */
  padding-left: 20px;
}

.input-edit-taskName input {
  padding-left: 20px;
  margin-left: 2vw;
  outline: 1px dotted #74747486;
}

.input-edit-taskName input::placeholder {
  /* padding-left: 10px; */
}

.colEdit-input-date input {
  width: 5vw;
  font-size: 12px;
  height: 4vh;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  text-align: center;
}
